$duration: .5s;

.m-p-g {
	
	&__thumbs {
		
		&-img {
			margin: 0;
			float: left;
			vertical-align: bottom;
			transition: all $duration cubic-bezier(0.23, 1, 0.32, 1);
			cursor: pointer;
			z-index: 1;
			position: relative;
			opacity: 0;
			-webkit-tap-highlight-color: rgba(black, 0);
			// filter: brightness(90%);
			will-change: opacity, transform;
			
			&.active {
				z-index: 50;
			}

			&.hide {
				transition-delay: 0.25s;
				opacity: 0;
			}
			
			&.layout-completed {
				opacity: 1;
			}
			
			&:hover {
				filter: brightness(100%);
			}
		}
	}
	
	&__fullscreen {
		position: fixed;
		z-index: 10;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		width: 100%;
		height: 100vh;
		background: rgba(black, 0);
		visibility: hidden;
		transition:
			background .5s cubic-bezier(0.23, 1, 0.32, 1),
			visibility .01s .5s linear;
		will-change: opacity, visibility;

		&.active {
			transition:
				background .5s cubic-bezier(0.23, 1, 0.32, 1),
				visibility .01s 0s linear;
			visibility: visible;
			background: rgba(black, 0.95);
		}

		&-img {
			position: absolute;
			transform-origin: left top;
			top: 50%;
			left: 50%;
			max-height: 100vh;
			max-width: 100%;
			visibility: hidden;
			z-index: 60;
			// -webkit-filter: saturate(0%);

			&.active {
				visibility: visible;
				z-index: 200;
				transition: transform .6s cubic-bezier(0.23, 1, 0.32, 1);
			}
			
			&.prev {
				transform: translate(-100%, -50%);
				opacity: 0;
			}
			
			&.next {
				transform: translate(-0%, -50%);
				opacity: 0;
			}
		}
	}
	
	&__controls {
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		z-index: 200;
		height: 20vh;
		background: linear-gradient(to top, transparent 0%, rgba(black, 0.55) 100%);
		opacity: 0;
		visibility: hidden;
		transition: all $duration cubic-bezier(0.23, 1, 0.32, 1);
		
		&.active {
			opacity: 1;
			visibility: visible;
		}
		
		&-close,
		&-arrow {
			appearance: none;
			border: none;
			background: none;

			&:focus {
				outline: none;
			}
		}

		&-arrow {
			position: absolute;
			z-index: 1;
			top: 0;
			width: 20%;
			height: 100vh;
			display: flex;
			align-items: center;
			cursor: pointer;
			-webkit-tap-highlight-color: rgba(black, 0);
			opacity: 0;
			
			&:hover {
				opacity: 1;
			}
			
			&--prev {
				left: 0;
				padding-left: 3vw;
				justify-content: flex-start;
			}
			
			&--next {
				right: 0;
				padding-right: 3vw;
				justify-content: flex-end;
			}
		}
		
		&-close {
			position: absolute;
			top: 3vh;
			left: 3vw;
			z-index: 5;
			cursor: pointer;
			-webkit-tap-highlight-color: rgba(black, 0);
		}
	}
}